<template>
	<div id="senior">
		<div v-show="loadingIsShow">
			<loading></loading>
		</div>
		<!--打开高级认证部分结束-->
		<div class="header_box">
			<div class="register_header">
				<p class="back" @click="$router.push('identity')">
					<i class="iconfont icon-zuojiantou"></i>
				</p>
				<p class="register_type">
					{{$t("userCenter.Documentmaterials")}}
				</p>
			</div>
		</div>
		<div class="oHeight"></div>
		<!--证件材料-->
		<div class="Documentmaterial box"> 
			<p class="title">{{$t("userCenter.AdvancedCertification")}}</p>
			<p class="text">{{$t("userCenter.Notexceed")}}</p> 	
		</div>
		<!--用户证件正面-->
		<div class="Document box">
			<p class="title">{{$t("userCenter.Frontalphoto")}}</p>
			<div class="content">
				<img :src="PositiveImg" v-show="PositiveImgIsShow" />
				<input type="file" @change="changePositive($event)" accept="image/png,image/gif,image/jpg,image/jpeg" />
				<i class="iconfont icon-shenfenzheng1" v-if="!PositiveImgIsShow"></i>
			</div>
		</div>

		<!--用户证件反面-->
		<div class="Document box">
			<p class="title">{{$t("userCenter.Reversephoto")}}</p>
			<div class="content">
				<img :src="NegativeImg" v-show="NegativeImgIsShow" />
				<input type="file" @change="changeNegative($event)" accept="image/png,image/gif,image/jpg,image/jpeg" />
				<i class="iconfont icon-shenfenzhengbeimian" v-if="!NegativeImgIsShow"></i>
			</div>
		</div>

		<!--手持证件照片-->
		<div class="Document box">
			<p class="title">{{$t("userCenter.Handwritten")}}</p>
			<div class="content">
				<img :src="HandheldImg" v-show="HandheldImgIsShow" />
				<input type="file" @change="changeHandheldImg($event)" accept="image/png,image/gif,image/jpg,image/jpeg" />
				<i class="iconfont icon-shouchishenfenzheng-copy" v-if="!HandheldImgIsShow"></i>
			</div>
			<div class="tishi">
				<i class="iconfont icon-gantanhao"></i> {{$t("userCenter.Handwrittensignature")}}
			</div>
		</div>

		<!--确认提交-->

		<div class="submit box">
			<button @click="SubmitReview">{{$t("otc.Confirm")}}</button>
		</div>

	</div>
</template>

<script>
	import axios from "axios"
	import { httpUrl } from "../../http_url/http_url"
	import loading from "../../public/loading"
	export default {
		data() {
			return {
				bankCardHeader: this.$t("userCenter.AdvancedCertification"),
				qr_img: "http://img.zcool.cn/community/01a4d2554441400000019ae96d8aaa.jpg",
				/*获取二维码*/
				menShow: false,
				/*蒙版*/
				isInden: false,
				/*判断有没有绑定身份信息*/
				toUrl: "",
				/*跳转链接*/
				loadingIsShow: false,
				/*是否出loading效果*/
				iframeIsShow: false,
				/*点击高级认证部分*/
				idcard_front: "",
				idcard_back: "",
				idcard_holt: "",
				PositiveImg: "",
				PositiveImgIsShow: false,
				NegativeImg: "",
				NegativeImgIsShow: false,
				HandheldImg: "",
				HandheldImgIsShow: false
			}
		},
		mounted() {
			this.refshCode();
		},
		components: { /*loading效果*/
			"loading": loading
		},
		methods: {
			changePositive(e) { /*上传正面图片*/
				var file = e.target.files[0];
				this.idcard_front = e.target.files[0];
				var windowURL = window.URL;
				var dataURL = windowURL.createObjectURL(file);
				this.PositiveImg = dataURL;
				this.PositiveImgIsShow = true;
			},
			changeNegative(e) { /*上传反面图片*/
				var file = e.target.files[0];
				this.idcard_back = e.target.files[0];
				var windowURL = window.URL;
				var dataURL = windowURL.createObjectURL(file);
				this.NegativeImg = dataURL;
				this.NegativeImgIsShow = true;
			},
			changeHandheldImg(e) { /*上传手持图片*/
				var file = e.target.files[0];
				this.idcard_holt = e.target.files[0];
				var windowURL = window.URL;
				var dataURL = windowURL.createObjectURL(file);
				this.HandheldImg = dataURL;
				this.HandheldImgIsShow = true;
			},
			async SubmitReview() { /*提交审核*/
				let _this = this;
				if(_this.idcard_front == "" || _this.idcard_back == "" || _this.idcard_holt == "") {
					_this.$message({
						message:_this.$t("bbjy.Pleaseuploadfullinfo"),
						type: 'error'
					});
					return false;
				}
				this.loadingIsShow = true;
				let config = await axios.get(httpUrl.uploadSet, {
					headers: {
						"Authorization": "Bearer " + _this.token
					}
				});
				function generateUID() {
					// I generate the UID from two parts here 		
					// to ensure the random number provide enough bits.
					var firstPart = (Math.random() * 46656) | 0;
					var secondPart = (Math.random() * 46656) | 0;
					firstPart = ("000" + firstPart.toString(36)).slice(-3);
					secondPart = ("000" + secondPart.toString(36)).slice(-3);
					return JSON.stringify(new Date()).split('T')[0].replace(/-|"/g,'')+firstPart + secondPart;
				}
				const uploadFile = async function (file){
					let param = new FormData(); //创建 form对象
					let fileName = file + generateUID()
					param.append("OSSAccessKeyId",config.data.data.accessKeyId);
					param.append("policy",config.data.data.policy);
					param.append("signature",config.data.data.signature);
					param.append("key",fileName);
					param.append('file', _this[file]); //通过append向form对象添加数据	
					// await Promise.resolve(''); 
					let res  = await axios.post(config.data.data.url, param, {
						headers: {
							'Content-Type': 'multipart/form-data'
						}
					})
					return fileName;
				};
				let uploadRes = await ['idcard_front','idcard_back','idcard_holt'].reduce(async function (pre,cur){
					let p = await pre;
					const path = await uploadFile(cur);
					p[cur] = config.data.data.url+'/'+path;
					// console.log(cur,path,pre);
					return pre;
				}, Promise.resolve({}));
				let res  = await axios.post(httpUrl.uploadCardPath, uploadRes, {
					headers: {
						"Authorization": "Bearer " + _this.token,
					}
				})
				if(res.data.status.code == 200) {
					_this.$router.push({
							name: "identity"
					})
				}
				console.log(res);
			},
			clickiframeIsShow() { /*点击退出iframe并且请求接口判断用户是否验证成功*/
				let _this = this;
				this.iframeIsShow = false; /*点击关闭iframe*/
				axios.get(httpUrl.getFaceToken, {
					headers: {
						"Authorization": "Bearer " + _this.token
					}
				}).then(function(res) {
					if(res.data.data.type > 0) {
						_this.$message.error({
							showClose: true,
							message: res.data.data.msg,
							type: "error",
							duration: 2000
						});
					} else {
						/*成功后跳转到身份认证页面*/
						_this.$router.push('identity');
					}

				}, function(err) {
					console.log(err)
				})
			},
			refshCode() {
				var _this = this;
				_this.loadingIsShow = true;
				axios.get(httpUrl.getFaceToken, {
					headers: {
						"Authorization": "Bearer " + _this.token
					}
				}).then(function(res) {
					_this.qr_img = res.data.data.qr; /*二维码*/
					_this.menShow = true;
					_this.toUrl = res.data.data.url; /*链接*/
					setTimeout(function() {
						_this.menShow = false;
					}, 2000)
					if(res.data.status.code == 101) {
						_this.isInden = true
					}
					_this.loadingIsShow = false;
				}, function(err) {
					console.log(err)
				})
			},
			toSenior() { /*点击跳转链接*/
				let that = this;
				that.iframeIsShow = true; /*显示高级认证部分*/
			},
			toSenior1() {
				let that = this;
				window.open(that.toUrl);
			}
		}
	}
</script>

<style lang="scss" scoped>
	$submit_color:#373959;
	/*登录线条*/

	$submit_borderColor:1px solid #9fa0af;
	/*按钮边框颜色*/

	$header_bg:#141738;
	$border_bottom: 1px solid #1b1d3e;
	$submit_color:#373959;
	/*头部边框*/

	#senior {
		color: #fff;
		background: #141738;
		padding-bottom: .05rem;
	}
	/*证件材料*/

	.Documentmaterial {
		width: 100%;
		box-sizing: border-box;
		padding: 0 .1rem;
		.title {
			line-height: .3rem;
			font-size: .15rem;
		}
		.text {
			line-height: .2rem;
		}
	}
	/*提交*/

	.submit {
		width: 2.7rem;
		height: .38rem;
		margin: .5rem auto;
		button {
			width: 100%;
			height: .38rem;
			border: $submit_borderColor;
			background: $submit_color;
			color: #fff;
			outline: none;
		}
	}

	.Document {
		box-sizing: border-box;
		padding: 0 .1rem;
		.title {
			width: 100%;
			height: .4rem;
			line-height: .4rem;
			box-sizing: border-box;
		}
		.content {
			width: 80%;
			height: 1.2rem;
			border: 1px dashed #fff;
			margin: 0 auto;
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			img {
				width: 100%;
				height: 100%;
				position: absolute;
				left: 0;
				top: 0;
				z-index: 6;
			}
			.iconfont {
				font-size: 1rem;
			}
			.icon-shenfenzhengbeimian {
				font-size: .65rem;
			}
			.icon-shouchishenfenzheng-copy {
				font-size: .9rem;
			}
			input {
				width: 100%;
				height: 100%;
				opacity: 0;
				position: absolute;
				z-index: 66;
			}
		}
		.tishi {
			margin-top: .2rem;
			line-height: .2rem;
			box-sizing: border-box;
			padding: 0 .25rem;
			.iconfont {
				color: red;
			}
		}
	}
	/*点击打开高级验证部分*/

	.iframe_box {
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 9999;
		background: #fff;
		color: #000;
		.iframe_header {
			width: 100%;
			height: .5rem;
			position: relative;
			line-height: .5rem;
			.close {
				margin-left: .1rem;
			}
			.text {
				position: absolute;
				left: 20%;
				top: 0;
				width: 60%;
				text-align: center;
				line-height: .5rem;
				font-size: .16rem;
			}
		}
	}
	/*判断用户是否绑定身份信息*/

	.isbdShow {
		position: fixed;
		left: 10%;
		top: 20%;
		width: 80%;
		background: #32355E;
		color: #fff;
		z-index: 999;
		.isbdShow_header {
			width: 100%;
			height: .4rem;
			line-height: .4rem;
			display: flex;
			justify-content: space-between;
			box-sizing: border-box;
			padding: 0 .1rem;
			.iconfont {
				font-size: .14rem;
			}
			border-bottom:1px solid #232555;
		}
		.isbdShow_content {
			width: 100%;
			line-height: .2rem;
			text-align: center;
			margin: .2rem 0;
			box-sizing: border-box;
			padding: 0 .25rem;
		}
		/*确认  取消*/
		.confimCancel {
			width: 100%;
			height: .4rem;
			line-height: .4rem;
			display: flex;
			border-top: 1px solid #232555;
			p {
				width: 50%;
				text-align: center;
				color: #fff;
			}
			p:nth-of-type(1) {
				color: #939BEE;
				border-right: 1px solid #232555;
			}
		}
	}
	/*蒙版*/

	.men_ban {
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
		background: rgba(0, 0, 0, 0.6);
		z-index: 66;
	}
	/*二维码绑定*/

	.bindingCode {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		.img {
			display: block;
			width: 1.2rem;
			height: 1.2rem;
			margin-top: .5rem;
		}
		.center {
			width: 100%;
			line-height: .2rem;
			text-align: center;
			margin-top: .3rem;
			color: #fff;
		}
	}
	/*提示文字内容*/

	.tishiText {
		width: 100%;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		p {
			width: 85%;
			line-height: .2rem;
			color: #6a72d5;
			.realod {
				color: #fff;
				margin-left: .1rem;
			}
		}
	}

	.borderColor {
		width: 100%;
		height: .1rem;
		background: #121528;
	}

	.submit {
		width: 2.7rem;
		height: .38rem;
		margin: .2rem auto;
		button {
			width: 100%;
			height: .38rem;
			border: $submit_borderColor;
			background: $submit_color;
			color: #fff;
			outline: none;
		}
	}
	/*撑开高度*/

	.oHeight {
		width: 100%;
		height: .5rem;
	}
	/*头部固定*/

	.header_box {
		width: 100%;
		height: 8.5%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1000;
		background: $header_bg;
	}

	.register_header {
		width: 100%;
		height: .5rem;
		line-height: .5rem;
		position: relative;
		color: #fff;
		.back {
			margin-left: .15rem;
		}
		.register_type {
			position: absolute;
			top: 0;
			left: 20%;
			width: 60%;
			text-align: center;
			font-size: .15rem;
			font-weight: 600;
		}
	}

	.box {
		height: 89.5%;
		background: $header_bg;
	}
</style>



