<template>
	<div id="realName">
		<div class="header_box">
			<div class="register_header">
				<p class="back" @click="$router.go(-1)">
					<i class="iconfont icon-zuojiantou"></i>
				</p>
				<p class="register_type">
					{{$t("userCenter.Verified")}}
				</p>
			</div>
		</div>
		<div class="oHeight"></div>
		<!--姓名 --开户银行---开户支行----银行卡号-->
		<div class="box" v-show="!userList.trueName">
			<ul class="userList">
				<li @click="popupVisible=true">
					<span class="name">{{$t("userCenter.Countryregion")}}</span>
					<p>{{countryInfo}}</p>
				</li>
				<li>
					<span class="name">{{$t("userCenter.Names")}}</span>
					<input type="text" v-model="trueName" :placeholder="$t('userCenter.Enterrealname')" />
				</li>
				<li>
					<span class="name">{{$t("userCenter.IDnumber")}}</span>
					<input type="text" v-model="myidCard" :placeholder="$t('userCenter.EnterIDcardnum')" />
				</li>
				<p class="tiShiError" v-show="cardError">{{$t("userCenter.IDcardillegal")}}</p>
			</ul>
			<div class="submit">
				<button @click="bindingIdentity" :disabled="!trueName||!myidCard">{{$t("capital.Confirm")}}</button>
			</div>
		</div>
		<div class="box" v-show="userList.trueName">
			<ul class="userList">
				<li>
					<span class="name">{{$t("userCenter.Countryregion")}}</span>
					<p>{{countryInfo}}</p>
				</li>
				<li>
					<span class="name">UID</span>
					<p>{{userList.user_id}}</p>
				</li>
				<li>
					<span class="name">{{$t("userCenter.Names")}}</span>
					<p>{{userList.trueName}}</p>
				</li>
				<li>
					<span class="name">{{$t("userCenter.IDnumber")}}</span>
					<p>{{userList.idCard}}</p>
				</li>
				<p class="tiShiError" v-show="cardError">{{$t("userCenter.IDcardillegal")}}</p>
			</ul>
		</div>
		<!--国家选择列表-->
		<mt-popup v-model="popupVisible" position="right">
			<div class="header_box">
				<div class="register_header">
					<p class="register_type">
            {{$t("userCenter.Choosenationality")}}
					</p>
					<p class="back" @click="popupVisible=false">
						<i class="iconfont icon-zuojiantou"></i>
					</p>
				</div>
			</div>
			<!--添加地址-->
		<div class="oHeight"></div>
			<!--列表-->
			<ul class="currencyList">
				<li v-for="(item,index) in countryList"  class="cardItem" @click="tabCurrency(index,item)">
					<span>{{item.name}}</span>
					<i class="iconfont icon-duihao" v-if="item.id==selectCountry"></i>
				</li>
			</ul>
		</mt-popup>
	</div>
</template>

<script>
	import axios from "axios"
	import { httpUrl } from "../../http_url/http_url"
	import header from "../../public/header"
	import $ from "jquery"
	export default {
		data() {
			return {
				bankCardHeader: this.$t("userCenter.Verified"),
				myidCard: "",
				/*我的身份证*/
				trueName: "",
				/*姓名*/
				cardError: false,
				/*身份证输入错误提示*/
				userList: {},
				/*获取用户信息*/
				countryInfo: "",
				/*国家*/
				countryList: [],
				/*国家列表*/
				popupVisible: false, /*选择国家*/
				selectCountry:""     /*选择国家*/
			}
		},
		methods: {
			bindingIdentity() { /*绑定身份证*/
				let _this = this;
				axios.post(httpUrl.setIdcard, {
					idCard: _this.myidCard,
					trueName: _this.trueName,
					country_id:_this.selectCountry
				}, {
					headers: {
						"Authorization": "Bearer " + _this.token
					}
				}).then(function(res) {
					/*判断是否绑定成功*/
					if(res.data.status.code == 200) {
						_this.$message({
							message: _this.$t("userCenter.RealNameSuccess"),
							type: 'success'
						});
						window.location.reload();
					}
				}, function(err) {
					console.log(err)
				})
			},
			tabCurrency(i, data) { /*切换币种*/
				let _this = this;
				$(".cardItem .icon-duihao").eq(i).show().parent(".cardItem").siblings(".cardItem").find(".icon-duihao").hide();
				_this.selectCountry = data.id;
				_this.countryInfo = data.name; /*国家*/
				_this.popupVisible = false;   /*选择后弹框消失*/
			},
		},
		mounted() { /*获取用户信息*/
			let _this = this;
			axios.get(httpUrl.userInfo, {
				headers: {
					"Authorization": "Bearer " + _this.token
				}
			}).then(function(res) {
				_this.userList = res.data.data;
				_this.countryInfo = res.data.data.countryInfo[0].name; /*国家*/
				_this.selectCountry = res.data.data.country_id;
			}, function(err) {
				console.log(err)
			})
			//获取国家列表
			axios.get(httpUrl.country).then(function(res) {
				_this.countryList = res.data.data;
			}, function(error) {
				console.log(error)
			})
		},
		components: {
			"my-header": header
		}
	}
</script>
<style lang="scss">
	#realName .mint-popup { background: #141738; }#realName .mint-popup-right { width: 100%; height: 100%; background: #141738; overflow-y: auto; }
</style>
<style lang="scss" scoped>
	$submit_color:#373959;
	/*登录线条*/

	$submit_borderColor:1px solid #9fa0af;
	/*按钮边框颜色*/

	$border_bottom: 1px solid #1b1d3e;
	/*列表边框颜色*/

	$placeholder:#6f77e0;
	/*文本提示颜色*/

	$slideRight:#171932;
	/*弹框样式*/

	$header_bg:#141738;
	/*整体的盒子*/

	#realName {
		width: 100%;
		height: 100%;
	}
	/*用户列表*/

	.userList {
		width: 100%;
		position: relative;
		li {
			width: 100%;
			height: .4rem;
			line-height: .4rem;
			color: #fff;
			display: flex;
			justify-content: space-between;
			box-sizing: border-box;
			padding: 0 .1rem;
			border-bottom: $border_bottom;
			input {
				width: 78%;
				border: none;
				background: none;
				outline: none;
				color: #fff;
			}
			input::-webkit-input-placeholder {
				color: $placeholder;
				text-align: right;
			}
		}
		.tiShiError {
			width: 100%;
			position: absolute;
			bottom: -.2rem;
			text-align: center;
			color: #d82d0f;
		}
	}

	.register_header {
		width: 100%;
		height: .5rem;
		line-height: .5rem;
		position: relative;
		color: #fff;
		background: rgb(20, 23, 56);
		border-bottom: .1rem solid #121528;
		.back {
			margin-left: .15rem;
		}
		.register_type {
			position: absolute;
			top: 0;
			left: 20%;
			width: 60%;
			text-align: center;
			font-size: .15rem;
			font-weight: 600;
		}
	}
	/*币种列表选项*/

	.currencyList {
		width: 100%;
		margin-top: .1rem;
		li {
			width: 100%;
			height: .37rem;
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-bottom: $border_bottom;
			color: #fff;
			box-sizing: border-box;
			padding: 0 .1rem;

		}
	}
	/*登录按钮*/

	.submit {
		width: 2.7rem;
		height: .38rem;
		margin: .5rem auto;
		button {
			width: 100%;
			height: .38rem;
			border: $submit_borderColor;
			background: $submit_color;
			color: #fff;
			outline: none;
		}
	}
	/*撑开高度*/

	.oHeight {
		width: 100%;
		height: .5rem;
	}
	/*头部固定*/

	.header_box {
		width: 100%;
		height: 8.5%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1000;
		background: $header_bg;
	}

	.register_header {
		width: 100%;
		height: .5rem;
		line-height: .5rem;
		position: relative;
		color: #fff;
		.back {
			margin-left: .15rem;
		}
		.register_type {
			position: absolute;
			top: 0;
			left: 35%;
			width: 30%;
			text-align: center;
			font-size: .15rem;
			font-weight: 600;
		}
	}

	.box {
		height: 89.5%;
		background: $header_bg;
		margin-top: .05rem;
	}
</style>



